    const getSanskritTextsFromBackend = async (textId) => {
      if (
        textId === "BV_UPG_NY_NYS_NVNYYK_PG_TKS" ||
        textId === "BV_UPG_NY_NYS_NVNYYK_PG_TKS_TKSD" ||
        textId === "BV_UPG_MIM_UMIM_AV_MG_BMS" ||
        textId === "BV_UPG_MIM_UMIM_AV_MG_BMS_SB" ||
        textId === "BV_UPG_MIM_UMIM_AV_MG_BMS_SB_PCP" ||
        textId === "BV_UPG_MIM_UMIM_AV_MG_BMS_SB_PCP_VVS" ||
        textId === "BV_UPG_MIM_UMIM_AV_PG_SL" ||
        textId === "BV_UPG_MIM_UMIM_AV_PG_VC" ||
        textId === "BV_UPG_MIM_UMIM_AV_PG_VS" ||
        textId === "BV_SH_SS_PS_RV" ||
        textId === "BV_GN_ARB" ||
        textId === "BV_GN_LVG" ||
        textId === "BV_GN_SS" ||
        textId === "BV_GN_KKY" ||
        textId === "BV_SH_SS_PS_MD" ||
        textId === "BV_UPG_YOG_YS" ||
        textId === "BV_UPG_MIM_UMIM_AV_MG_UPS_ISV" ||
        textId === "BV_UPG_MIM_UMIM_AV_IG_SVS" ||
        textId === "BV_UPG_MIM_UMIM_AV_IG_SP"
      ) {
        try {
          const temp = await axios.get(
            "sanskrit-gallery/granthapp/api/sktextsViewTestRecursion/" + textId
          );

          if (temp.data === "") {
            alert("hihi yes");
          }
          setJsonMongoSanskritTexts(temp.data);

          setLineId(lineId);
          setNtextID1(NtextID);
        } catch (err) {
          alert(err);
        }
      }
    };

    async function callerFun() {
      console.log("callerFun");
      console.log(
        "Before getSanskritTextsFromBackend titleFromNavigation",
        NtextID,
        lineId
      );
      await getSanskritTextsFromBackend(NtextID);
      console.log("Caller");
    }

    callerFun();
